.homesecWrap{
    

    .article2{
        
        display: flex;
        .row{
            ul{display: flex;
                flex-wrap: wrap;    //줄바꿈 정렬
                padding:30px 0;
                li{
                    padding:20px;
                    width: 33.33%;
                    a{
                        display: flex;
                        img{
                            width: 30%;
                            margin-right: 10px ;
                            border-radius: 10px;
                        }
                        div{
                            align-self: center;
                        }
                    }
                    
                }
            }
            .lifeStyle{
                h2{
                    font-size: 20px;
                }
                ul{display: flex;
                    flex-wrap: wrap;    //줄바꿈 정렬
                    padding:30px 0;
                    li{
                        padding:20px;
                        width: 33.33%;
                        img{
                            width: 100%;
                            margin-right: 10px ;
                            border-radius: 10px;
                        }
                    }
                }
            }
        }
    }
    .article3{
        background: #000;
        color: #fff;
        padding: 100px 0;
        .row{
            display: flex;
            div{width: 52%;
                img{
                    width: 100%;
                    padding: 10px 10px 0;
                    border-radius: 30px 30px 0 0;
                    vertical-align: middle; //p테그와 img 테그의 빈틈 제거
                }             
                p{  
                    margin: 0px 10px;
                    background: #999;
                    padding: 20px;
                    border-radius: 0vw 0vw 3vw 3vw;
                } 
            }
            .right{
                width: 48%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                .rightFst{
                    display: flex;
                    flex-direction: row;
                }
                div{
                    width: 100%;
                }
            } 
        }
    }
}